import React, { useState } from 'react';
import { Plus, Trash2 } from 'lucide-react';

const FormBuilder = ({ onUpdate }) => {
  const [elements, setElements] = useState([]);
  const [selectedElement, setSelectedElement] = useState(null);
  const [theme, setTheme] = useState('Custom Theme');
  const [primaryColor, setPrimaryColor] = useState('#FF00BFF');
  const [backgroundColor, setBackgroundColor] = useState('#FFFFFF');
  const [font, setFont] = useState('Inter');

  const handleAddElement = () => {
    const newElement = {
      id: Date.now(),
      type: 'dropdown',
      label: 'New dropdown',
      options: [
        { label: 'Basic Plan', price: '2.99', id: Date.now() },
        { label: 'Pro Plan', price: '7.99', id: Date.now() + 1 },
        { label: 'Premium Plan', price: '14.99', id: Date.now() + 2 }
      ]
    };
    setElements([...elements, newElement]);
    setSelectedElement(newElement);
  };

  const handleAddPlan = (elementId) => {
    setElements(elements.map(element => {
      if (element.id === elementId) {
        return {
          ...element,
          options: [...element.options, { label: '', price: '', id: Date.now() }]
        };
      }
      return element;
    }));
  };

  const handlePlanChange = (elementId, optionId, field, value) => {
    setElements(elements.map(element => {
      if (element.id === elementId) {
        return {
          ...element,
          options: element.options.map(option => {
            if (option.id === optionId) {
              return { ...option, [field]: value };
            }
            return option;
          })
        };
      }
      return element;
    }));
  };

  const handleDeletePlan = (elementId, optionId) => {
    setElements(elements.map(element => {
      if (element.id === elementId) {
        return {
          ...element,
          options: element.options.filter(option => option.id !== optionId)
        };
      }
      return element;
    }));
  };

  const handleDeleteElement = (elementId) => {
    setElements(elements.filter(element => element.id !== elementId));
    if (selectedElement?.id === elementId) {
      setSelectedElement(null);
    }
  };

  return (
    <div className="flex">
      <div className="w-2/3 p-6">
        <div className="flex justify-between items-center mb-6">
          <h1 className="text-2xl font-semibold">Pokkadot Form Builder</h1>
        </div>

        <div className="flex items-center mb-8">
          <div className="flex items-center">
            <div className="bg-pink-500 text-white rounded-full h-8 w-8 flex items-center justify-center">
              1
            </div>
            <span className="ml-2 font-medium">Build Form</span>
          </div>
          <div className="flex-1 mx-4 border-t-2 border-gray-200"></div>
          <div className="flex items-center">
            <div className="bg-gray-200 rounded-full h-8 w-8 flex items-center justify-center">
              2
            </div>
            <span className="ml-2 font-medium">Configure Payment</span>
          </div>
          <div className="flex-1 mx-4 border-t-2 border-gray-200"></div>
          <div className="flex items-center">
            <div className="bg-gray-200 rounded-full h-8 w-8 flex items-center justify-center">
              3
            </div>
            <span className="ml-2 font-medium">Preview</span>
          </div>
        </div>

        <button
          onClick={handleAddElement}
          className="mb-6 bg-pink-500 text-white px-4 py-2 rounded-md hover:bg-pink-600 flex items-center gap-2"
        >
          <Plus className="w-5 h-5" />
          ADD ELEMENTS
        </button>

        {elements.map(element => (
          <div key={element.id} className="mb-6 bg-white rounded-lg shadow-md p-6">
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-lg font-medium">{element.label}</h2>
              <button
                onClick={() => handleDeleteElement(element.id)}
                className="text-gray-400 hover:text-red-500"
              >
                <Trash2 className="w-5 h-5" />
              </button>
            </div>

            {element.options.map(option => (
              <div key={option.id} className="flex items-center gap-4 mb-4">
                <input
                  type="radio"
                  name={`element-${element.id}`}
                  className="w-4 h-4 text-pink-500"
                />
                <div className="flex-1">
                  <input
                    type="text"
                    placeholder="Option Name"
                    value={option.label}
                    onChange={(e) => handlePlanChange(element.id, option.id, 'label', e.target.value)}
                    className="w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-pink-500"
                  />
                </div>
                <div className="w-32">
                  <div className="relative">
                    <span className="absolute left-3 top-2">$</span>
                    <input
                      type="number"
                      value={option.price}
                      onChange={(e) => handlePlanChange(element.id, option.id, 'price', e.target.value)}
                      className="w-full px-3 py-2 pl-6 border rounded-md focus:outline-none focus:ring-2 focus:ring-pink-500"
                    />
                  </div>
                </div>
                <button
                  onClick={() => handleDeletePlan(element.id, option.id)}
                  disabled={element.options.length === 1}
                  className="p-2 text-gray-400 hover:text-red-500 disabled:opacity-50"
                >
                  <Trash2 className="w-5 h-5" />
                </button>
              </div>
            ))}

            <button
              onClick={() => handleAddPlan(element.id)}
              className="flex items-center gap-2 text-pink-500 hover:text-pink-600 mt-4"
            >
              <Plus className="w-5 h-5" />
              ADD PLAN
            </button>
          </div>
        ))}
      </div>

      <div className="w-1/3 p-6 bg-gray-50 border-l">
        <div className="flex gap-4 mb-6">
          <button className="px-4 py-2 text-pink-500 border-b-2 border-pink-500">
            DESIGN
          </button>
          <button className="px-4 py-2 text-gray-500">
            ADVANCED
          </button>
        </div>

        <div className="space-y-6">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Theme
            </label>
            <select
              value={theme}
              onChange={(e) => setTheme(e.target.value)}
              className="w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-pink-500"
            >
              <option>Custom Theme</option>
            </select>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Colors
            </label>
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-xs text-gray-500 mb-1">
                  Primary Color
                </label>
                <input
                  type="color"
                  value={primaryColor}
                  onChange={(e) => setPrimaryColor(e.target.value)}
                  className="w-full h-10 rounded-md cursor-pointer"
                />
              </div>
              <div>
                <label className="block text-xs text-gray-500 mb-1">
                  Background Color
                </label>
                <input
                  type="color"
                  value={backgroundColor}
                  onChange={(e) => setBackgroundColor(e.target.value)}
                  className="w-full h-10 rounded-md cursor-pointer"
                />
              </div>
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Font
            </label>
            <select
              value={font}
              onChange={(e) => setFont(e.target.value)}
              className="w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-pink-500"
            >
              <option>Inter</option>
            </select>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FormBuilder;
